      <div class="header-content">
        <img src="../assets/musiclogo.png" alt="" class="musicimg">
        <div class="music-text"></div>
        <div class="nav-list">
          <div :class="['findmusic', showitem.includes('/homepage') ? 'activeselect' : '']" @click="gotoHome">
            <div class="menu-icon" v-if="showitem.includes('/homepage')"></div>
          </div>
          <div :class="['mymusic', showitem == '/myMusic' ? 'activeselect' : '']" @click="gotomyMusic">
            <div class="menu-icon" v-if="showitem == '/myMusic'"></div>
          </div>
          <div :class="['care', showitem == '/friendPage' ? 'activeselect' : '']" @click="gotofriendPage">
            <div class="menu-icon" v-if="showitem == '/friendPage'"></div>
          </div>
          <div :class="['shop', showitem == '/shoppage' ? 'activeselect' : '']" @click="gotoshopPage">
            <div class="menu-icon" v-if="showitem == '/shoppage'"></div>
          </div>
          <div :class="['musician', showitem == '/songerpage' ? 'activeselect' : '']" @click="gotomusicianPage">
            <div class="menu-icon" v-if="showitem == '/songerpage'"></div>
          </div>
          <div :class="['commend', showitem == '/commendpage' ? 'activeselect' : '']" @click="gotocommendPage">
            <div class="menu-icon" v-if="showitem == '/commendpage'"></div>
          </div>
          <div class="download" @click="gotodownload">
            <div class="menu-icon" v-if="showitem == '/downLoad'"></div>
          </div>
        </div>
        <div class="music-search">
          <img src="../assets/search.png" alt="">
          <input type="text" placeholder="///">
        </div>
        <div class="creator-center"></div>
        <div class="loginBtn" @click="showLoginBtn"></div>
      </div>
    </div>

     
  </div>
</template>

<script>
export default {
  components: {},
  props: {
  },
  data() {
    return {
      showitem: '',
      showLogin:false,

    };
  },
  computed: {},
  watch: {
    '$route.path'(newPath) {
      this.showitem = newPath

    }
  },
  created() {
    this.showitem = this.$route.path;
    console.log(this.$router);



  },
  mounted() { },
  methods: {
    gotoHome() {
      this.showitem = '/findmusic';
      this.$router.push('/');

    },
    gotofriendPage() {
      this.$router.push('/friendPage');
      this.showitem = '/friendPage';
    },
    gotomyMusic() {
      this.showitem = '/myMusic';
      this.$router.push('/myMusic');
    },
    gotodownload() {
      this.showitem = '/downLoad';
      this.$router.push('/downLoad');
    },
    gotoshopPage() {
      this.$router.push('/shoppage');
    },
    gotomusicianPage() {
      this.$router.push('/songerpage');
    },
    gotocommendPage() {
      this.$router.push('/commendpage');
    },
    showLoginBtn() {
      this.showLogin = true;
      this.$bus.$emit('activelogin',this.showLogin)
    }
  },
};
</script>
