<template>
    <div class="pagenum-container">
        <div class="pagenum-item">
            <div :class="['prv-btn',activePage==0?'stop':'']" @click="prvpage">
                
            </div>
            <div :class="['page-button', activePage == 0 ? 'selectPage' : '']" @click="activePage = 0">1</div>
            <div :class="['more-page',activePage<5?'morepagenone':'']" v-show="activePage > 4">...</div>
            <div :class="['page-button', activePage == num ? 'selectPage' : '']" v-for="num in pageNum - 2" :key="num"
                v-show="min < num && num < max" @click="activePage = num">{{ num + 1 }}</div>
            <div :class="['more-page', activePage >= this.pageNum - 7 ? 'morepagenone' : '']" v-show="activePage != pageNum">...
            </div>
            <div :class="['page-button', activePage == pageNum-1 ? 'selectPage' : '']" @click="activePage = pageNum-1">{{
                pageNum }}
            </div>
            <div :class="['next-btn',activePage==pageNum-1?'stop':'']" @click="nextPage">
                
            </div>
        </div>
    </div>
</template>

<script>
export default {
    props: {
        MusicNum: {
            type: Number,
            default: 0
        },
    },
    data() {
        return {
            min: 0,
            max: 8,
            pageNum: 1,
            activePage: 0,
        };
    },
    computed: {},
    watch: {
        activePage(newval) {
            if (newval >= 5 && newval <= this.pageNum - 8) {
                this.max = this.activePage
                this.min = this.activePage
                this.max += 4
                this.min -= 4
            } else if (newval <= this.pageNum && newval >= this.pageNum - 9) {
                this.max = this.pageNum
                this.min = this.pageNum - 9
            }
            else if (newval < 5) {
                this.min = 0;
                this.max = 8;

            }
           
            this.$emit('activePage',newval)
        }
    },
    created() {
        this.pageNum = Math.ceil(this.MusicNum / 35)
    },
    mounted() { },
    methods: {
        prvpage(){
            if(this.activePage==0){
             this.activePage=0
            }else{
                this.activePage--
            }
        },
        nextPage(){
             if(this.activePage==this.pageNum-1){
             this.activePage=this.pageNum-1
            }else{
                this.activePage++
            }
        }
    }
};

</script>

<style scoped>
.pagenum-container {
    width: 900px;
    height: 25px;
    margin: 20px 0;
    padding-left: calc(50% - 250px);
}
.pagenum-item {
    width: 470px;
    display: flex;
    justify-content: space-between;
}
.prv-btn {
    display: flex;
    width: 47px;
    height: 24px;
    border: 1px solid #ccc;
    font-size: 12px;
    color:#000;
    text-align: center;
    padding-left: 22px;
    line-height: 24px;
    background: url('@/assets/button.png');
    background-position: 0 -620px;
    margin: 0 1px 0 2px;
    border-radius: 2px;
}
.page-button {
    width: 23px;
    height: 22px;
    color: #333;
    border: 1px solid #ccc;
    border-radius: 2px;
    text-align: center;
    line-height: 25px;
    font-size: 12px;
    font-weight: 400;
}

.more-page {
    width: 24px;
    height: 24px;
    color: #333;
    text-align: center;
    line-height: 24px;
    font-size: 12px;
    font-weight: 400;
}

.next-btn {
    display: flex;
    width: 47px;
    height: 24px;
    border: 1px solid #ccc;
    font-size: 12px;
    text-align: center;
        color:#000;
    padding-right: 17px;
    padding-left: 5px;
    line-height: 24px;
    margin: 0 1px 0 2px;
    border-radius: 2px;
    background: url('@/assets/button.png');
    background-position: -75px -560px;

}

.selectPage {
    border-color: #A2161B;
    color: #fff;
    cursor: default;
    background: url('@/assets/button.png');
    background-position: 0 -650px;
}

.morepagenone {
    display: none;
}
.stop{
        color: #cacaca;
}
</style>