<!--  -->
<template>
<header>
      <div class="header-line"></div>
      <div class="nav-menu">
        <div class="menu-item">
          <div @click="gotohome" :class="index=='/homepage/findmusic'?'active-btn':''"></div>
          <div @click="gotoRankPage" :class="index=='/homepage/rankpage'?'active-btn':''"></div>
          <div @click="gotomusiclist" :class="index=='/homepage/playlist'?'active-btn':''"></div>
          <div @click="gotodjRadio" :class="index=='/homepage/djRadio'?'active-btn':''"></div>
          <div @click="gotosonger" :class="index=='/homepage/songerpage'?'active-btn':''"></div>
          <div @click="gotoalbum" :class="index=='/homepage/newalbum'?'active-btn':''"></div>
        </div>
      </div>
      <div class="menu-line"></div>
    </header>
</template>

<script>
export default {
 
 data() {
 return {
   index:''
 };
 },
 created(){
this.index=this.$route.path
 },
 watch:{
'$route.path'(newview){
  this.index=newview
}
 },
methods:{
   gotoRankPage() {
    this.index='/homepage/rankpage'
      this.$router.push('/homepage/rankpage');
    },
    gotohome(){
      this.index='/homepage/findmusic'
      this.$router.push('/homepage/findmusic');
    },
    gotomusiclist(){
      this.index='/homepage/playlist'
        this.$router.push('/homepage/playlist');
    },
    gotodjRadio(){
      this.index='/homepage/djRadio'
       this.$router.push('/homepage/djRadio');
    },
    gotosonger(){
      this.index='/homepage/songerpage'
      this.$router.push('/homepage/songerpage');
    },
    gotoalbum(){
      this.index='/homepage/newalbum'
      this.$router.push('/homepage/newalbum');
    }
    
}
};
</script>

<style  scoped>
header {
    min-width: 1800px;
  height: 35px;
  margin-top: -5px;
}

.nav-menu {
  
  height: 35px;
  background-color: #c20c0c;
  width: 100%;
}

.menu-item {
  margin-left: 520px;
width: 550px;
  height: 35px;
  
  align-items: center;
  display: flex;
  justify-content: space-between;
  color: #fff;
}
.menu-item div{
  cursor: pointer;
  font-size: 12px;
  align-items: center;
  margin: 0 17px;
  padding: 0 13px;
  
}

.menu-line {
  height: 1px;
  width: 100%;
  background-color: #a40011;
}
.active-btn{
  background: #9B0909;
  border-radius: 20px;
  height: 20px;

}
</style>