import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)
const store = new Vuex.Store({
    state: {
        indexPlay:-1,
        showRank:""
    },
    mutations:{
        changeIndexplay(state,data){
            state.indexPlay=data
        },
        changeShowRank(state,data){
            state.showRank=data
        }
    }
})

export default store