<template>
 <div class='errorpage'>
 
     <img src="@/assets/404.png" alt="" class="error">
     <div class="back"><span>{{ time }}</span></div>
     <div class="goto" @click="goto"> </div>
 </div>
</template>

<script>
export default {

 components: {},
 props: {},
 data() {
 return {
 time:5,
 timer:null
 };
 },
 computed: {},
 watch: {},
 created() {
this.timer=setInterval(() => {
    if(this.time<1){
        this.$router.back()
    }
    this.time-=1
}, 1000);
 },
 mounted() {},
 beforeDestroy(){
clearInterval(this.timer)
 },
 methods: {
    goto(){
        this.$router.go(-1);
        clearInterval(this.timer)
    }
 }
};
</script>

<style lang="less" scoped>
    .errorpage{
        width: 100%;
       position: relative;
    }
.error{
    text-align: center;
   margin-left: 400px;
    height: 531px;
    
}
.back{
    font-size: 20px;
    text-align: center;
   position: absolute;
   top:450px;
   left: calc(50% - 70px);
    cursor: pointer;
    span{
        color: red;
    }
}

.goto{
 font-size: 20px;
 width: 100px;
 height: 30px;
 text-align: center;
 line-height: 30px;
    text-align: center;
   position: absolute;
   top:480px;
   left: calc(50% - 30px);
   cursor: pointer;
   background: url('@/assets/index.png');
   background-position: 0 -195px;
}
</style>