<template>
    <div class='loginpage'>
        <div class="login-header">
            <div class="login-text"></div>
            <img src="data:image/png;base64,"
                alt="" class="close" @click="close">
        </div>

        <div class="login-input">
            <div class="toregister" @click="gotoLogin">? <span></span></div>
            <div class="username">
                <input type="text" placeholder="" v-model="userInfo.username">
            </div>
            <div class="number">
                <input type="text" placeholder="" v-model="userInfo.phoneNum">
            </div>
            <div class="password">
                <input type="password" placeholder="" v-model="userInfo.password">
            </div>
            <div class="repassword">
                <input type="password" placeholder="" v-model="userInfo.repassword">
            </div>

            <div class="loginBtn" @click="registerBtn"></div>
        </div>
    </div>
</template>

<script>
export default {

    components: {},
    props: {

    },
    data() {
        return {
            userInfo: {
                username: '',
                phoneNum: '',
                password: '',
                repassword: '',
                birth: '2025-11-1',
            },
            closeRegister: false,
            gotologin: true
        };
    },
    computed: {},
    watch: {},
    created() { },
    mounted() { },
    methods: {
        registerBtn() {
            const passwordReg = /^(?=.*[a-zA-Z])(?=.*\d).+$/;
            var flag=true
            if (this.userInfo.username == '') {
                flag=false
                alert('')
            } else if (this.userInfo.password != this.userInfo.repassword) {
                flag=false
                alert('')
            } else if (!passwordReg.test(this.userInfo.password)) {
                flag=false
                alert('');
            } else if (this.userInfo.phoneNum.length != 11 || this.userInfo.phoneNum.slice(0, 1) != 1) {
                flag=false
                alert('')
            } else {
                this.$axios.post('http://10.9.88.47:8888/wyy-yin/user/signUp', this.userInfo, { headers: { 'Content-Type': 'application/x-www-form-urlencoded' } })
                    .then(res => {
                        alert(res.data.message)
                         this.userInfo.password=''
                this.userInfo.repassword=''
                    })
            }
            if(flag==false){
                this.userInfo.password=''
                this.userInfo.repassword=''
            }
        },


        close() {
            this.$emit('closeRegister', this.closeRegister)
        },
        gotoLogin() {
            this.$emit('gotologin', this.closeRegister, this.gotoLogin)
        }
    }
};
</script>

<style lang="less" scoped>
.loginpage {
    width: 536px;
}

.toregister {
    font-size: 20px;
    color: #000;
    width: 200px;
    margin-left: auto;
    cursor: pointer;

    span {
        color: red;
    }
}

.login-header {
    width: 502px;
    height: 38px;
    display: flex;
    justify-content: space-between;
    padding: 0 18px 0 18px;
    border-bottom: 1px solid #191919;
    border-radius: 4px 4px 0 0;
    background: #2d2d2d;
    font-weight: bold;
    font-size: 14px;
    color: #fff;
    line-height: 38px;

    .close {
        width: 16px;
        height: 16px;
        opacity: 0.6;
        cursor: pointer;
        margin: auto 0;
    }
}

.login-input {
    width: 536px;
    height: 320px;
    padding-top: 30px;
    padding-bottom: 53px;
    background-color: #f4f4f4;

    .username,
    .password,
    .repassword,
    .birthday,
    .sex,
    .number {
        width: 200px;
        margin: 0 auto;
    }

    .username input {
        margin-top: 20px;
        width: 200px;
        height: 30px;
        border: none;
        outline: none;
    }

    .number input {
        margin-top: 20px;
        width: 200px;
        height: 30px;
        border: none;
        outline: none;
        height: 31px;

    }

    .password input {
        margin-top: 30px;
        border: none;
        outline: none;
        width: 200px;
        height: 30px;
        margin-bottom: 20px;
    }

    .loginBtn {

        width: 100px;
        height: 30px;
        color: #fff;
        border-radius: 7px;
        background-color: aqua;
        text-align: center;
        line-height: 30px;
        font-size: 16px;
        margin: 0 auto;
        background-size: cover;
        background: url('@/assets/index.6faff6e0.png');
        background-position: 0 -195px;
        cursor: pointer;
    }

    .repassword input {
        width: 200px;
        height: 31px;
        outline: none;
        border: none;
        margin-bottom: 20px;
    }

}
</style>