import Vue from "vue";
import Router from 'vue-router';
import RankPage from "@/views/RankPage/RankPage.vue";
import DownLoad from "@/views/downLoad/DownLoad.vue";
import MyMusic from "@/views/myMusic/MyMusic.vue";
import PlayList from "@/views/PlayListPage/PlayList.vue";
import FriendPage from "@/views/friendPage/FriendPage.vue";
import HomePage from "@/views/Home/HomePage.vue";
import FindMusic from "@/views/Home/FindMusic.vue";
import DjRadio from "@/views/djRadio/DjRadio.vue";
import SongerPage from "@/views/songer/SongerPage.vue";
import NewAlbum from "@/views/album/NewAlbum.vue";
import ErrorPage from "@/views/ErrorPage.vue";

Vue.use(Router)

const originalPush = Router.prototype.push;
Router.prototype.push = function push(location) {
  return originalPush.call(this, location).catch(err => err);
};

const router = new Router({
  mode: 'hash',
  routes: [
    {
      path: '/',
      redirect: '/homepage',
    },
    {
      path: '/homepage',
      component: HomePage,
      redirect: '/homepage/findmusic',
      children: [{
        path: '/homepage/rankpage',
        component: RankPage
      },
      {
        path: '/homepage/findmusic',
        component: FindMusic
      },
      {
        path: '/homepage/playlist',
        component: PlayList
      },
      {
        path: '/homepage/djRadio',
        component: DjRadio
      },
      {
        path: '/homepage/songerpage',
        component: SongerPage
      },
       {
        path: '/homepage/newalbum',
        component: NewAlbum
      },
    ]
    },
    {
      path: '/friendPage',
      component: FriendPage
    },

    {
      path: '/myMusic',
      component: MyMusic
    },
    {
      path: '/downLoad',
      component: DownLoad
    },
    {
      path: '/*',
      component: ErrorPage
    },

  ]
})


export default router