<template>
    <div class="PlayMusic-container">
        <audio ref="playmusic" @loadedmetadata="handleMetadataLoaded" @timeupdate="handleTimeUpdate"></audio>
        <div 
            :class="['playmusic', activemusicPlay || showlock === 1 ? 'showplay' : 'hiddenplay']"
            @mouseover="showmusicplay" 
            @mouseleave="nonemusicplay"
        >
            <div class="play-container">
                <div class="play-btn">
                    <div class="prv"></div>
                    <div :class="['play', playactive ? 'beginicon' : '']" @click="playMusic"></div>
                    <div class="next"></div>
                </div>
                <div class="music-info">
                    <div class="photo">
                        <img src="" alt="">
                    </div>
                    <div class="progress-bar" ref="progressBar" @click="changePlay">
                        <div class="progress"></div>
                        <div class="control" 
                            :style="{ left: `${getProgressPercent()}%` }" 
                            @mousedown="startDragProgress"
                        ></div>
                        <div class="playprogress" :style="{ width: `${getProgressPercent()}%` }"></div>
                    </div>
                    <div class="time">
                        <div class="play-time">{{ musicplayTime }}</div>/
                        <div class="alltime">{{ musicallTime }}</div>
                    </div>
                </div>
                <div class="music-icons">
                    <div class="oper">
                        <div class="pip"></div>
                        <div class="add"></div>
                        <div class="share"></div>
                    </div>
                </div>
                <div class="music-control-icon">
                    <div class="volume"></div>
                    <div class="change"></div>
                    <div class="list">1</div>
                    <div class="quality"></div>
                </div>
                <div class="lock">
                    <div class="lock-icon" v-show="showlock === 1" @click="showlock = 0"></div>
                    <div class="close-icon" v-show="showlock === 0" @click="showlock = 1"></div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    data() {
        return {
            musicallTime: '00:00',
            musicplayTime: '00:00',
            playactive: false,
            showlock: 0,
            activemusicPlay: false,
            musicList: [],
            nowPlayMusic: '',
            musicalltime: 0,
            musicplaytime: 0,
            musicplay: null,
            isDragging: false,
        };
    },
    watch: {
        '$store.state.indexPlay'(newval) {
            this.$axios.get('/api/musiclist')
                .then((res) => {
                    this.musicList = res.data.data.music;
                    if (newval != -1) {
                        this.showlock = 1;
                        this.playactive = true;
                        this.nowPlayMusic = this.musicList[newval].musicUrl;
                        this.musicplay = require(`@/assets/musicList/${this.nowPlayMusic}.mp4`);
                        this.$refs.playmusic.src = this.musicplay;
                        this.$refs.playmusic.play()
                    }
                });
        },
    },
    created() {
      
        document.addEventListener('mousemove', this.handleDragProgress);
        document.addEventListener('mouseup', this.endDragProgress);
    },
    beforeDestroy() {
        document.removeEventListener('mousemove', this.handleDragProgress);
        document.removeEventListener('mouseup', this.endDragProgress);
    },
    methods: {
        showmusicplay() {
            this.activemusicPlay = true;
        },
        nonemusicplay() {
            this.activemusicPlay = false;
        },
        playMusic() {
            this.playactive = !this.playactive;
            this.playactive ? this.$refs.playmusic.play() : this.$refs.playmusic.pause();
        },
        handleMetadataLoaded() {
            this.musicalltime = this.$refs.playmusic.duration;
            this.formatTime(this.musicalltime, 'all');
        },
        handleTimeUpdate() {
            
            if (!this.isDragging) {
                this.musicplaytime = this.$refs.playmusic.currentTime;
                this.formatTime(this.musicplaytime, 'play');
            }
        },
      
        formatTime(time, type) {
            const m = Math.floor(time / 60);
            const s = Math.floor(time % 60);
            const formatted = `${m.toString().padStart(2, '0')}:${s.toString().padStart(2, '0')}`;
            if (type == 'all') {
                this.musicallTime = formatted;
            } else {
                this.musicplayTime = formatted;
            }
        },
        changePlay(e) {
            const progressBar = this.$refs.progressBar;
            const barWidth = progressBar.offsetWidth;
            const percent = e.offsetX / barWidth;
            this.updatePlayProgress(percent);
        },
        startDragProgress() {
            this.isDragging = true;
        },
        handleDragProgress(e) {
            if (!this.isDragging || !this.musicalltime) return;
            const progressBar = this.$refs.progressBar;
            const barRect = progressBar.getBoundingClientRect(); 
            const barWidth = barRect.width;
            const offsetX = e.clientX - barRect.left;
            const percent = Math.max(0, Math.min(1, offsetX / barWidth));
            this.updatePlayProgress(percent, true); 
        },
       
        endDragProgress() {
            if (this.isDragging) {
                this.isDragging = false;
                
                this.$refs.playmusic.currentTime = this.musicplaytime;
            }
        },
        
        updatePlayProgress(percent, isDragging = false) {
            const newTime = percent * this.musicalltime;
            this.musicplaytime = newTime;
            this.formatTime(newTime, 'play');
            if (!isDragging) {
                this.$refs.playmusic.currentTime = newTime;
            }
        },
        getProgressPercent() {
            const percent = (this.musicplaytime / this.musicalltime) * 100;
            return Math.max(0, Math.min(100, percent - (22 / this.$refs.progressBar?.offsetWidth || 466) * 100));
        },
    },
};
</script>

<style lang="less" scoped>
.playmusic {
    display: flex;
    align-items: center;
    width: 100%;
    height: 53px;
    position: fixed;
    background: url('@/assets/playbar.png');
    margin-right: 67px;
    background-position: 0 0;
    background-repeat: repeat-x;
    z-index: 999;
    bottom: -46px;

    .play-container {
        margin: 0 auto;
        width: 1030px;
        height: 47px;
        display: flex;
        align-items: center;

        .play-btn {
            width: 137px;
            height: 42px;
            display: flex;
            align-items: center;

            .prv {
                width: 28px;
                height: 28px;
                margin-right: 8px;
                margin-top: 5px;
                text-indent: -9999px;
                background: url('@/assets/playbar.png');
                background-position: 0 -130px;
            }

            .play {
                width: 36px;
                height: 36px;
                margin-right: 8px;
                background: url('@/assets/playbar.png');
                background-position: 0 -204px;
            }

            .beginicon {
                width: 36px;
                height: 36px;
                margin-right: 8px;
                background: url('@/assets/playbar.png');
                background-position: 0 -165px;
            }

            .next {
                width: 28px;
                height: 28px;
                margin-right: 8px;
                margin-top: 5px;
                text-indent: -9999px;
                background: url('@/assets/playbar.png');
                background-position: -80px -130px;
            }
        }
    }
}

.music-info {
    height: 100%;
    display: flex;

    .photo {
        width: 34px;
        height: 35px;
        margin: auto 5px;

        background: url('@/assets/playbar.png');
        background-position: 0 -80px;
    }

    .progress-bar {
        display: flex;
        margin-top: 30px;
        position: relative;

        .progress {
            height: 9px;
            width: 466px;
            background: url('@/assets/statbar.png');
            background-position: right 0;
        }

        .playprogress {
            position: absolute;
            z-index: 9;
            top: 0;
            left: 0;
            width: 0;
            height: 9px;
            background: url('@/assets/statbar.png');
            background-position: left -66px;
        }

        .control {
            position: absolute;
            top: -7px;
            left: -10px;
            width: 22px;
            height: 24px;
            background: url('@/assets/iconall.png');
            background-position: 0 -250px;

        }

    }

    .time {
        margin-top: 26px;
        margin-left: 10px;
        display: flex;
        font-size: 12px;
        color: #797979;

        .play-time {
            font-size: 12px;
            color: #A1A1A1;
        }
    }
}

.music-icons {
    display: flex;
    align-items: center;

    .oper {
        width: 87px;
        height: 36px;
        display: flex;
        justify-content: space-evenly;
        align-items: flex-end;
        margin-left: 20px;

        .pip {
            width: 25px;
            height: 25px;
            background: url('@/assets/pip.png');
            background-position: 0 0;
        }

        .add {
            width: 25px;
            height: 25px;
            background: url('@/assets/playbar.png');
            background-position: -88px -163px;
        }

        .share {
            width: 25px;
            height: 25px;
            background: url('@/assets/playbar.png');
            background-position: -114px -163px;
        }
    }
}

.music-control-icon {
    width: 173px;
    height: 36px;
    display: flex;
    align-items: flex-end;
    margin-left: 13px;
    justify-content: space-evenly;
    position: relative;

    .volume {
        width: 25px;
        height: 25px;
        background: url('@/assets/playbar.png');
        background-position: -2px -248px;
    }

    .change {
        width: 25px;
        height: 25px;
        background: url('@/assets/playbar.png');
        background-position: -66px -248px;
    }

    .list {
        width: 38px;
        padding-left: 21px;
        height: 25px;
        text-align: center;
        line-height: 25px;
        color: rgb(102, 102, 102);
        font-size: 12px;

        background: url('@/assets/playbar.png');
        background-position: -42px -68px;
    }

    .quality {
        width: 41px;
        height: 25px;
        background: url('@/assets/audio-quality.png');
        background-size: cover;
    }


}

.lock {
    right: 30px;
    position: absolute;
    margin-bottom: 15px;
    width: 52px;
    height: 67px;
    background: url('@/assets/playbar.png');
    background-position: 0 -380px;

    .lock-icon {
        width: 18px;
        height: 18px;
        margin-left: 15px;
        margin-top: 5px;
        background: url('@/assets/playbar.png');
        background-position: -100px -380px;
    }

    .close-icon {
        width: 18px;
        height: 18px;
        margin-left: 15px;
        margin-top: 5px;
        background: url('@/assets/playbar.png');
        background-position: -80px -380px;
    }
}

.showplay {
    bottom: 0;
    transition: all 0.5s ease
}

.hiddenplay {
    bottom: -46px;
    transition: all 0.5s ease
}
</style>