<template>
    <div class='loginpage'>
        <div class="login-header">
            <div class="login-text"></div>
            <img src="data:image/png;base64,"
                alt="" class="close" @click="closeLogin">
        </div>

        <div class="login-input">
            <div class="toregister" @click="gotoRegsiter">? <span></span></div>
            <div class="username">
                <input type="text" placeholder="" v-model="userInfo.username"> 
            </div>
            <div class="password">
                <input type="password" placeholder="" v-model="userInfo.password">
            </div>
            <div class="loginBtn" @click="gotologin"></div>
        </div>
    </div>
</template>

<script>
export default {

    components: {},
    props: {
        activeRegister: {
            type: Boolean,
            default: false
        }
    },
    data() {
        return {
            changeLogin: false,
            showRegistr: false,
            userInfo: {
                username: '',
                password: '',
                
            }
        };
    },
    computed: {},
    watch: {},
    created() { },
    mounted() { },
    methods: {
        closeLogin() {
            this.changeLogin = false
            this.$emit('changeLogin', this.changeLogin)

        },
        gotoRegsiter() {
            this.showRegistr = true
            this.changeLogin = false
            this.$emit('toRegister', this.showRegistr, this.changeLogin)

        },
        gotologin(){
            this.$axios.post('http://10.9.88.47:8888/wyy-yin/user/login',this.userInfo,{headers:{'Content-Type': 'application/x-www-form-urlencoded' }})
            .then(res=>{
                alert(res.data.message)
                this.userInfo.username=''
                this.userInfo.password=''
                
                if(res.data.message==""){
                 this.changeLogin=false
                  this.$emit('changeLogin', this.changeLogin)
                }
            })
        }
    }
};
</script>

<style lang="less" scoped>
.loginpage {
    width: 536px;
}

.toregister {
    font-size: 20px;
    color: #000;
    width: 200px;
    margin-left: auto;
    cursor: pointer;

    span {
        color: red;
    }
}

.login-header {
    width: 502px;
    height: 38px;
    display: flex;
    justify-content: space-between;
    padding: 0 18px 0 18px;
    border-bottom: 1px solid #191919;
    border-radius: 4px 4px 0 0;
    background: #2d2d2d;
    font-weight: bold;
    font-size: 14px;
    color: #fff;
    line-height: 38px;

    .close {
        width: 16px;
        height: 16px;
        opacity: 0.6;
        cursor: pointer;
        margin: auto 0;
    }
}

.login-input {
    width: 536px;
    height: 220px;
    padding-top: 30px;
    padding-bottom: 53px;
    background-color: #f4f4f4;

    .username,
    .password {
        width: 200px;
        margin: 0 auto;
    }

    .username input {
        margin-top: 30px;
        width: 200px;
        height: 30px;
        border: none;
        outline: none;
    }

    .password input {
        margin-top: 30px;
        border: none;
        outline: none;
        width: 200px;
        height: 30px;
        margin-bottom: 30px;
    }

    .loginBtn {
        width: 100px;
        height: 30px;
        color: #fff;
        border-radius: 7px;
        background-color: aqua;
        text-align: center;
        line-height: 30px;
        font-size: 16px;
        margin: 0 auto;
        background-size: cover;
        background: url('@/assets/index.6faff6e0.png');
        background-position: 0 -195px;
        cursor: pointer;
    }
}
</style>