<template>
   <div class='playlist'>
      <main>
         <div class="music-list-title">
            <TitleMusic title="" >
               <template #min-title>
                     <div style="display: flex;">
                  <div class="menu-container">
                     <div class="menu-btn"></div>
                     <div class="down"></div>
                  </div>
                  <div class="rone"></div>
               </div>
               </template>
             <div class="hot-text"></div>
            </TitleMusic>
         </div>

         <div class="music-list">
            <div class="music-list-item" v-for="(item, index) in CurrrentIndex" :key="index">
               <HotMusic :viewsnum="item.viewsnum"
               :musicimg='item.musicimg'>
                  <template #title>
                     <div class="music-title">
                        {{ item.title }}
                     </div>
                  </template>

                  <template #min-title>
                     <div class="by-icon">by</div>
                     {{ item.mintitle }}
                     
                  </template>

               </HotMusic>
            </div>
         </div>
         <SelectPage :MusicNum="MusicNum" @activePage="showPageContent"
         v-if="MusicNum> 0"></SelectPage>
      </main>
   </div>
</template>

<script>
import HotMusic from '@/components/HotMusic.vue';
import SelectPage from '@/components/SelectPage.vue';
import TitleMusic from '@/components/TitleMusic.vue';

export default {

   components: {
      TitleMusic,
      HotMusic,
      SelectPage
   },
   props: {},
   data() {
      return {
         nowPage:0,
         MusicNum:0,
         showMusic:1,
         HotMusicList: [],
      };
   },
   computed: {
      CurrrentIndex(){
         let begin=this.nowPage*30
         let end=this.nowPage*30+30
         return this.HotMusicList.slice(begin,end)
      }
   },
   watch: {

   },
   created() { 
    this.getMusicList()
   },
   mounted() { },
   methods: {
      getMusicList(){
         let Max=3
           this.$axios.get('https://m1.apifoxmock.com/m1/7588881-7327295-default/music/%7BHotmusicList%7D')
      .then((res)=>{
         this.HotMusicList=[...res.data.data];
         this.MusicNum=this.HotMusicList.length
      })
      .catch(()=>{
         if(Max>=0){
            this.getMusicList
         }
      })
      },
      showPageContent(data){
          this.nowPage=data
          console.log(this.nowPage);
          console.log(data);
      }
   }
};
</script>

<style scoped>
.playlist {
   min-width: 1800px;
   margin: 0;
   padding: 0;
   background-color: #f5f5f5;

}

main {
  
   padding: 40px;
   width: 900px;
   margin: 0 auto;
   background-color: white;
}

.menu-container {
   display: flex;
   width: 86px;
   height: 31px;
   padding-right: 5px;
   margin: 2px 0 0 12px;
   background: url('@/assets/button2.png') no-repeat 0 9999px;
   background-position: 0 -141px;
}

.rone {
   width: 5px;
   height: 31px;
   background: url('@/assets/button2.png') no-repeat 0 9999px;
   background-position: right -99px;
}
.hot-text{
   width: 46px;
   font-size: 12px;
   height: 29px;
   text-align: center;
   line-height: 29px;
   background: url('@/assets/button.png');
       background-position: 0 0;
    border-radius: 3px;
    color: #fff;
}
.menu-btn {
   width: 40px;
   height: 11px;
   font-size: 10px;
   color: #0c73c2 !important;
   padding: 0 10px 0 15px;
   text-align: center;
   line-height: 31px;
}

.down {
   width: 8px;
   height: 5px;
   margin: auto 0;
   background: url('@/assets/icon.png');
   background-position: -70px -543px;
}

.music-list {
   width: 980px;
   margin-left: -50px;
   display: flex;
   flex-wrap: wrap;
 
    margin-top: 40px;
}

.music-list-item {
   padding: 0 0 30px 50px;
}

.music-title {
   display: -webkit-box;
   -webkit-box-orient: vertical;
   -webkit-line-clamp: 1;
   overflow: hidden;
   text-overflow: ellipsis;
}

.by-icon {
   width: 13px;
   height: 14px;
   color: #999;
   font-size: 12px;

}



</style>